//Revision 2023
.b-btn-push {
  text-decoration: none;
  outline: none;
  border: none;
  border-radius: 4px;
  box-shadow: 0 3px rgba(0, 0, 0, 0.3), 0 -4px rgba(0, 0, 0, 0.2) inset;
  transition: box-shadow 20ms linear, background 0.5s;
  &::after {
    box-shadow: 0 -4px rgba(0, 0, 0, 0.2) inset;
    border-radius: 4px;
  }
  &::before {
    box-shadow: 0 -4px rgba(0, 0, 0, 0.2) inset;
    border-radius: 4px;
    background-color: none;
  }
  &:active {
    box-shadow: 0 1px rgba(0, 0, 0, 0.6);
    transform: translateY(2px);
  }
  &[color="alternate"] {
    box-shadow: 0 3px rgb(0 0 0 / 40%);
    &:active {
      box-shadow: 0 1px rgba(0, 0, 0, 0.6);
    }
  }
  //Revision 2023

  // Beta
  // Sombras variadas
  &::after {
    // box-shadow: 0 -2px 0 2px rgb(0 0 0 / 15%) inset;
    box-shadow: none !important;
  }
  // mejorado
  &[align="up"] {
    transform: translateY(-3px);
    &:active {
      transform: translateY(-1px);
    }
  }
}
