@import '../colors/colors';
@import "../grid/variables";

.b-header {
    z-index: 32 !important;
    font-size: 14px;
    background-color: $gray;
    flex-wrap: nowrap;
    height: var(--b-header-height);
    flex: 1 0 100%;
    position: sticky;
    top: 0;

    &[mobile] {
        &>*:is([bx-logo]) {
            margin: 0;
        }

        &>*:is([bx-fixed]):not([show]){
            display: none;
        }

        [bx-mobile-bar] {
            display: none;
        }
    }

    &,
    &>[bx-logo],
    &>[bx-content],
    &>[bx-fixed] {
        display: flex;
        z-index: 1;
    }

    &>[bx-logo] {
        box-shadow: 10px 0px 5px -5px rgba(0, 0, 0, 0.1);
        color: #fff;
        position: sticky;
        align-items: center;
        padding: 5px;

        * {
            margin-right: 5px;

            &:last-child {
                margin-right: 0;

            }
        }

        img {
            max-width: 4rem;
            max-height: 100%;
        }
    }

    &>* {
        margin-right: 5px;

    }

    &>*:last-child {
        margin-right: 0;
    }

    &>[bx-content] {
        align-items: start;
        overflow: overlay;
        width: 100%;

        &>* {
            margin-right: 5px;
        }

        &>*:last-child {
            margin-right: 0;
        }
    }

    &>[bx-content],
    &>[bx-fixed] {
        align-items: center;
        padding: .25rem 1rem;
        margin-right: 0;
    }

    &>[bx-fixed] {
        box-shadow: -10px 0px 5px -5px rgba(0, 0, 0, 0.1);
        flex: 1;
        right: 0;
        justify-content: flex-end;

        &>* {
            margin-right: 5px;
        }

        &>*:last-child {
            margin-right: 0;
        }
    }

    &[skin="blur"] {
        box-shadow: 0 2px 5px -4px black;
    }

    &>[bx-mobile] {
        color: #fff;
        padding: 5px;
        margin-left: auto;
        font-size: 22px;
        display: none;
        justify-content: center;
        align-items: center;

        * {
            width: 32px;
            height: 32px;
        }

    }
}

@keyframes example {
    from {
        transform: translateX(-100%);
    }

    to {
        transform: translateX(-100%)
    }
}

@media (max-width: $sm) {
    .b-header[mobile] {
        z-index: 40;

        &>[bx-logo]{
            box-shadow: none;
            flex: 1;

        }
        &>[bx-fixed]{
            box-shadow: none;
            flex: 0;

        }

        &>[bx-content] {
            transform: translateX(-100%);
            flex-direction: column;
            position: absolute;
            background: white;
            height: 100vh;
            top: 0;
            left: 0;
            bottom: 0;
            align-items: start;
            align-content: start;
            max-width: 250px;
            width: 100%;
            animation-name: example;
            animation-duration: 0.2s;
            transition: 0.2s;
            overflow: auto;
            z-index: 2;
            padding: .25rem;

            &>[class*="b-btn"] {
                width: 100%;
                margin-bottom: 5px;
            }

            &[opened] {
                transform: translateX(0);
                box-shadow: 2px 8px 20px rgba(0, 0, 0, 0.24);
            }

            &>[bx-mobile-bar] {
                display: block;
                color: inherit;
                width: 100%;
            } 
        }
        &>[bx-mobile] {
            display: flex;
        }
    }
}

@media (min-width: $sm) {
    .b-header[mobile] {
        [bx-fixed] {
            display: inherit !important;
        }
    }
}