@import '../colors/colors';
//Revision 2023
.b-btn-gate {
    border: none;
    font-weight: 700;
    box-shadow: 0 0 0 2px $gray inset;
    overflow: hidden;
    background: transparent !important;
    transition: box-shadow 0.3s ;
    z-index: 0;
    transition-delay: .1s, 0s;
    transition-property: color, box-shadow;
    transition-duration: .3s;
    transition-timing-function: cubic-bezier(0.7, 0, 0.2, 1);
    &:active {
        box-shadow: none !important;
    }
    &[closed]:hover::before {
        transform: translate3d(0, 0%, 0) !important;         
    }
    &:not([closed]){
        &:hover::before {
            transform: translate3d(0, -102%, 0);
            
            filter: none !important;
        }
        &[dir="1"]::before,
        &[dir="3"]::before,
        &[dir="7"]::before,
        &[dir="9"]::before{
            transition: transform 0.5s cubic-bezier(0.7, 0, 0.2, 1);
        }    
        
        &[dir="3"]::before, &[dir="7"]::before{
            transform: rotate(225deg) scale(2,3);
        }
        &[dir="1"]::before, &[dir="9"]::before{
            transform: rotate(315deg) scale(2,3);
        }

        
        &[dir="4"]:hover::before {
            transform: translate3d(-102%, 0, 0);
        }
        &[dir="6"]:hover::before {
            transform: translate3d(102%, 0, 0);
        }
        &[dir="8"]:hover::before {
            transform: translate3d(0, 102%, 0);
        }
        &[dir="3"]:hover::before {
            transform: translateX(102%) rotate(225deg) translateY(102%);
        }
        &[dir="7"]:hover::before {
            transform: translateX(-102%) rotate(225deg) translateY(-102%);
        }
        &[dir="9"]:hover::before {
            transform: translateX(102%) rotate(315deg) translateY(102%);
        }
        &[dir="1"]:hover::before {
            transform: translateX(-102%) rotate(315deg) translateY(-102%);
        }
        
    }
    
    &:hover {
        &:is([no-efx]){
            color: $gray !important;
        }
    }
    &[closed]:before{
        transform: translate3d(0, -102%, 0);
    }
    &::before {
        content: '';
        background: white;
        transition: transform 0.3s cubic-bezier(0.7, 0, 0.2, 1);
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: -1;
    }
}
//Revision 2023