//Revision 2023
@use "btn";
@use "gate";
//Revision 2023
@use "push";

@use "hover";
@use "gradient";
@use "neon";
//Only for dash
@use "complement-focus";
//Propiedades Propias
@use "btn_color";
@use "btn_border";
@use "btn_outline";

//
a[class^="b-btn"] {
    display: inline-block;
    text-decoration: none;
    color: initial;
}

[class^="b-btn"] {
    -webkit-tap-highlight-color: #0000;
    user-select: none;
    text-align: center;
    padding: .5rem 1rem;

    &:disabled {
        color: rgb(93, 93, 93) !important;
        background: rgb(183, 183, 183) !important;
    }

    &:not([class*='div-']) {
        min-width: 5rem;
    }

    position: relative;
    cursor: pointer;
    font-size: 14px;
    flex: 0 0 auto;
    white-space: nowrap;

    &:visited {
        color: initial;
    }

    &:not(.b-btn-neon) {

        &[color="default"],
        &[color="blue"],
        &[color="gray"],
        &[color="red"],
        &[color="green"],
        &[color="indigo"],
        &[color="pink"],
        &[color="dark-pink"],
        &[color="orange"],
        &[color="purple"],
        &[color="turq"],
        &[color="dark-gray"],
        &[color="black"] {
            color: white;
        }
    }

    &[size='sm'] {
        padding: .2rem .8rem;
        min-width: revert;
    }

    &[size='lg'] {
        font-size: 20px;
        min-width: revert;
    }

    &[size='xl'] {
        font-size: 24px;
        min-width: revert;
    }

    &[icon] {
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 0.2rem;
        width: 24px;
        height: 24px;
        min-width: revert;
    }
}

[color="dark"] [class^="b-btn"] {

    &[color="default"],
    &[color="alternate"] {
        color: white;
    }
}

.b-btn:not([no-efx]),
.b-btn-push:not([no-efx]) {
    overflow: hidden;
}