.b-btn-hover {
    margin: 0;
    border: none;
    outline: none;
    appearance: none;
    cursor: pointer;
    position: relative;
    font-size: 14px;
    line-height: 1.4;
    padding: 13px 20px;
    border-radius: 4px;
    transition: box-shadow 0.1s, top 0.1s;
    box-shadow: 0 3px 0 rgba(0, 0, 0, 0.3);
    top: 0;
    background: #fff;
    &:hover {
        box-shadow: 0 3px 0 transparent;
        top: 3px;
    }
    &:active {
        background: #f2f2f2;
    }
}

.b-btn-icon {
    padding: 0 !important;
    min-width: unset !important;
    border-radius: 50%;
    width: 24px;
    height: 24px;
    background: none;
    border: none;
    cursor: default;
    outline: none;
    transition: all 0.2s;
    display: flex;
    justify-content: center;
    align-items: center;
    &:hover {
        background: rgba(0, 0, 0, .2);
        color: white;
    }
}